
@font-face {
  font-family: 'iconfont';  /* project id 335733 */
  src: url('//at.alicdn.com/t/font_0017cvbbftysc3di.eot');
  src: url('//at.alicdn.com/t/font_0017cvbbftysc3di.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_0017cvbbftysc3di.woff') format('woff'),
  url('//at.alicdn.com/t/font_0017cvbbftysc3di.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_0017cvbbftysc3di.svg#iconfont') format('svg');
}
* {
  padding: 0 0;
  margin: 0 0;
}
body {
  font-size: 14px;
  font-family: "Helvetica Neue", Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "HeitiSC", "WenQuanYi Micro Hei", sans-serif;
}
.warp {
  border-spacing: 0;
  height: 100%;
  width: 100%;
}
a {
  text-decoration: none;
}
a:active, a:hover {
}
nav {
  background-color: #fff;
  height: 60px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 60px;
  z-index: 1;
}
nav > .left {
  line-height: 60px;
  float: none;
  font-size: 20px;
  font-weight: 700;
  max-height: 60px;
  display: inline-block;
  padding: 0 20px;
  background-color: #4cb6cb;
  color: #fff;
  width: 200px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
nav > .left > span {
  padding-left: 10px;
}
ul {
  list-style: none;
}
.iconfontyyy {
  font-family: "iconfont", serif !important;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  font-size: 24px;
}
nav > .contr {
  width: 54px;
  height: 100%;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  text-align: center;
}
nav > .contr:hover {
  background-color: #edf2f3;
}
nav > .right {
  float: right;
  height: 100%;
}
nav > .right > li {
  float: left;
  min-width: 60px;
  height: 60px;
  cursor: pointer;
  text-align: center;
}
nav > .right > li:hover {
  background-color: #edf2f3;
}
nav > .right > .option {
  position: relative;
  padding: 0 20px;
}
nav > .right > .option > ul {
  width: 160px;
  position: absolute;
  top: 60px;
  right: 0;
  z-index: 1;
  -webkit-box-shadow: 0 0 1px 0;
          box-shadow: 0 0 1px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #fff;
  /*opacity: 0;*/
  /*transition: all 1s;*/
}
nav > .right > .option > ul > li {
  height: 37px;
  line-height: 37px;
}
.hid {
  width: 70px !important;
}
.content_home {
  width: 100%;
  bottom: 0;
  top: 60px;
  position: absolute;
}
.content_home > .table_display {
  width: 100%;
  height: 100%;
  display: table;
}
.content_home > .table_display > .left {
  width: 200px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #181f24;;
  height: 100%;
  overflow: hidden;
  display: table-cell;
  /*transition: width 1s;*/
}
.content_home > .table_display > .left > a {
  color: #627b8c !important;
  display: inline-block;
  width: 100%;
  height: 42px;
  line-height: 42px;
  font-size: 12px;
  padding-left: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.content_home > .table_display > .left > ul {
  width: 100%;
}
.content_home > .table_display > .left > ul > li {
  width: 100%;
  height: 37px;
  line-height: 37px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  overflow: hidden;
  text-align: center;
}
.content_home > .table_display > .left > ul > li > i {
  font-size: 16px;
  display: inline-block;
  width: 100%;
}
.iconfontyyycg {
  font-size: 28px !important;
}
.content_home > .table_display > .left > ul > li > span {
  color: #8ca1af;
  font-size: 14px;
  padding-left: 20px;
  font-weight: 700;
}
.content_home > .table_display > .left > ul > li:hover {
  background-color: #2c3942;
}
.content_home > .table_display > .left > ul > li:hover > span {
  color: #ffffff;
}
.content_home > .table_display > .right {
  background: #F2F4F8;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: table-cell;
  vertical-align: top;
  height: 100%;
}
.noticenum {
  display: inline-block;
  background: #f00;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 13px;
  right: 16px;
  line-height: 15px;
  font-size: 80%;
  border-radius: 50%;
  color: #fff;
}
.userImg {
  width: 40px;
  height: 40px;
  display: inline-block;
  border-radius: 50%;
  top: 10px;
  position: relative;
}



